.App {
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100vh;

  .welcome {
    color: gray;
    font-size: 0.3rem;
  }

  .phone {
    overflow: auto;
    flex: 1;
  }

  .bottom {
    background-color: #f6f6f6;
    border: 1px solid #dcdcdc;
    margin-top: -1px;
    padding: 10px;
    font-size: 0.3rem;
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
